body,
html {
  background: #f0f0f0;
  position: relative;
}

header,
section,
footer {
  padding: 80px 0;
  z-index: 1;
  position: relative;
}

h1 {
  font: 400 90px/1 "Comic CAT", sans-serif;
  text-transform: uppercase;
  color: #0082c9;
}

.element-animation i {
  display: inline-block;
  position: relative;
  overflow: hidden;
}
.element-animation span {
  transition: all 0.5s;
  opacity: 1;
  transform: translateY(100%);
  transition-delay: inherit;
  display: inline-block;
}
.element-animation.element-show span {
  opacity: 1;
  transform: translateY(0);
}

.arrow-up {
  display: none;
}

.btn__red {
  padding: 16px 23px;
  border: none;
  border-radius: 55px;
  background: #ff7f56;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  font: 400 18px/1 "SF Pro Display", sans-serif;
  color: #fff;
  transition: all 0.4s;
  width: max-content;
}
.btn__red span {
  display: block;
  transition: all 0.4s;
  position: relative;
}
.btn__red i {
  position: relative;
  overflow: hidden;
}
.btn__red i::before {
  width: 100%;
  display: block;
  text-align: center;
  content: attr(link);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 200%);
  transition: all 0.4s;
}
.btn__red:hover {
  background: #ff6635;
}
.btn__red:hover i::before {
  transform: translate(-50%, -50%);
}
.btn__red:hover span {
  transform: translateY(-200%);
}
.btn__trans {
  border: 1px solid #bdd9e8;
  padding: 15px 22px;
  border-radius: 55px;
  background: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  font: 400 18px/1 "SF Pro Display", sans-serif;
  transition: all 0.3s;
  width: max-content;
}
.btn__trans span {
  display: block;
  transition: all 0.3s;
  position: relative;
  color: #0082c9;
}
.btn__trans i {
  position: relative;
  overflow: hidden;
}
.btn__trans i::before {
  width: 100%;
  display: block;
  text-align: center;
  content: attr(link);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 200%);
  transition: all 0.3s;
  color: #fff;
}
.btn__trans:hover {
  border: 1px solid #0082c9;
  background: #0082c9;
}
.btn__trans:hover i::before {
  transform: translate(-50%, -50%);
}
.btn__trans:hover span {
  transform: translateY(-200%);
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  box-sizing: content-box;
}
.container__title {
  font: 500 15px/1.2 "SF Pro Display", sans-serif;
  text-transform: uppercase;
  color: #0082c9;
}
.container__wrapper {
  display: grid;
  grid-template-columns: 282px 1fr;
  gap: 20px;
  align-items: start;
}
.container__pad {
  padding-left: 304px;
  margin-bottom: 122px;
}

.nav {
  padding: 19px 0;
  z-index: 15;
  position: relative;
  background: #f0f0f0;
  top: 0;
  transition: all 0.3s;
}
.nav__logo {
  display: block;
  width: 100%;
  max-width: 216px;
}
.nav__logo img {
  width: 100%;
}
.nav .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}
.nav__btn {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  z-index: 20;
}
.nav .btn__red::before {
  content: "";
  display: block;
  width: 18px;
  height: 19px;
  background: url("../img/heart_trans.svg") center no-repeat;
  background-size: contain;
}
.nav .btn__red:hover {
  background-size: contain;
}
.nav .btn__red:hover::before {
  background: url("../img/heart_full.svg") center no-repeat;
}
.nav__overflow {
  background: rgba(0, 0, 0, 0.45);
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 10;
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s;
}
.nav__menu {
  position: absolute;
  top: calc(100% + 15px);
  max-height: calc(100vh - 120px);
  overflow-y: hidden;
  left: 20px;
  right: 20px;
  z-index: 15;
  background: #f0f0f0;
  border-radius: 30px;
  padding: 30px 25px 25px 25px;
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s;
}
.nav__menu::before {
  content: "";
  display: block;
  position: absolute;
  width: 1px;
  height: 100%;
  max-height: 100%;
  left: 50%;
  bottom: 0;
  top: 0;
  background: #bdd9e8;
}
.nav__menu_soc {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  gap: 10px;
  margin: 120px 0 0;
}
.nav__menu_soc_wrapper {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.nav__menu_soc a {
  position: relative;
  width: 56px;
  height: 56px;
  border-radius: 100%;
  border: 1px solid #bdd9e8;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  transition: all 0.4s;
}
.nav__menu_soc a i {
  position: relative;
  overflow: hidden;
}
.nav__menu_soc a i::before {
  content: "";
  position: relative;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 200%);
  transition: all 0.4s;
  background: var(--bg) center no-repeat;
  background-size: contain;
  width: 26px;
  height: 26px;
  display: block;
  position: absolute;
}
.nav__menu_soc a img {
  width: 26px;
  height: 26px;
  display: block;
  position: relative;
  transition: all 0.4s;
}
.nav__menu_soc a:hover {
  border: 1px solid #0082c9;
  background: #0082c9;
}
.nav__menu_soc a:hover i::before {
  transform: translate(-50%, -50%);
}
.nav__menu_soc a:hover img {
  transform: translateY(-200%);
}
.nav__menu ul {
  display: grid;
  grid-template-columns: 1fr;
  gap: 30px;
}
.nav__menu ul a {
  font: 500 35px/1.2 "SF Pro Display", sans-serif;
  color: #0082c9;
  transition: all 0.3s;
}
.nav__menu ul a path {
  transition: all 0.3s;
}
.nav__menu ul a:hover {
  color: #82aec5;
}
.nav__menu ul a:hover path {
  fill: #82aec5;
}
.nav__menu ol {
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
  max-height: 0;
  overflow: hidden;
}
.nav__menu ol a {
  font: 400 22px/1.3 "SF Pro Display", sans-serif;
  color: #4686a8;
}
.nav__menu ol a:hover {
  color: #82aec5;
}
.nav__menu_list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 50px;
  align-items: start;
  position: relative;
}
.nav__menu_btn {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav__menu_btn.active path:nth-child(2) {
  opacity: 0;
}
.nav__menu_btn.active + ol {
  max-height: none;
  padding: 30px 0;
  border-bottom: 1px solid #bdd9e8;
}
.nav.active {
  z-index: 15;
  position: sticky;
  top: 0;
  left: 0;
  width: 100vw;
}
.nav.active .btn__trans {
  background: #fff;
}
.nav.active .btn__trans:hover {
  background: #0082c9;
}
.nav.active .nav__overflow, .nav.active .nav__menu {
  opacity: 1;
  pointer-events: all;
}
.nav.fix {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  transition: all 0.3s;
  box-shadow: 0px -15px 20px 0px #000;
  transform: translateY(-100%);
}
.nav.vis {
  transform: none;
}

.header {
  padding-top: 77px;
}
.header__wrapper {
  margin: 13px 0 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}
.header__wrapper img {
  width: 100%;
  border-radius: 30px;
  height: 405px;
  object-fit: cover;
  opacity: 0;
  transition: all 0.5s 1s;
  transform: translateY(50px);
}
.header__slider {
  margin: 34px 0 0;
}
.header__name {
  font: 400 90px/0.9 "Comic CAT", sans-serif;
  text-transform: uppercase;
  color: #0082c9;
  max-width: calc(100% - 100px);
  width: 850px;
}
.header__name i {
  display: inline-block;
  position: relative;
  overflow: hidden;
}
.header__name span {
  display: inline-block;
  opacity: 1;
  transition: all 0.5s 0.5s;
  transform: translateY(100%) !important;
}
.header__text {
  font: 400 18px/1.4 "SF Pro Display", sans-serif;
  color: #4686a8;
  max-width: 444px;
  opacity: 0;
  transition: all 0.5s 1s;
  transform: translateY(50px);
}
.header .btn__trans {
  margin: 28px 0 0;
  opacity: 0;
  transition: all 0.3s, transform 0.5s 1s, opacity 0.5s 1s;
  transform: translateY(50px);
}
.header__slide {
  opacity: 0 !important;
  transition: all 0.5s 0s !important;
}
.header__slide.swiper-slide-active {
  opacity: 1 !important;
}
.header__slide.swiper-slide-active .btn__trans {
  opacity: 1;
  transform: translateY(0);
}
.header__slide.swiper-slide-active .header__wrapper img {
  opacity: 1;
  transform: translateY(0);
}
.header__slide.swiper-slide-active .header__name span {
  transform: translateY(0) !important;
}
.header__slide.swiper-slide-active .header__text {
  opacity: 1;
  transform: translateY(0);
}
.header .swiper-button-prev,
.header .swiper-button-next {
  opacity: 1;
  width: 57px;
  height: 47px;
  margin: 0;
  right: auto;
  top: auto;
  transform: none;
  left: 0;
  bottom: 0;
}
.header .swiper-button-prev path,
.header .swiper-button-next path {
  transition: all 0.3s;
}
.header .swiper-button-prev::after,
.header .swiper-button-next::after {
  content: none;
}
.header .swiper-button-prev:hover path, .header .swiper-button-prev.swiper-button-disabled path,
.header .swiper-button-next:hover path,
.header .swiper-button-next.swiper-button-disabled path {
  fill: #82aec5;
}
.header .swiper-button-next {
  left: 82px;
}
.header .swiper-pagination {
  width: max-content;
  left: auto;
  top: auto;
  transform: none;
  right: 0;
  margin: 0;
  bottom: 435px;
  font: 500 35px/1.2 "SF Pro Display", sans-serif;
  color: #bdd9e8;
}
.header .swiper-pagination .swiper-pagination-current {
  color: #0082c9;
}

.fees__wrapper {
  border-bottom: 1px solid #bdd9e8;
}
.fees__prev {
  transition: all 0.3s;
  position: relative;
  overflow: hidden;
  border-radius: 20px;
}
.fees__prev img {
  width: 292px;
  height: 281px;
  object-fit: cover;
}
.fees__info {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap: 8px;
}
.fees__info p {
  color: #0082c9;
  transition: all 0.3s;
}
.fees__for {
  font: 400 16px/1.3 "SF Pro Display", sans-serif;
  color: #5d98b9 !important;
}
.fees__name {
  font: 500 35px/1.2 "SF Pro Display", sans-serif;
}
.fees__price {
  font: 400 20px/1.3 "SF Pro Display", sans-serif;
  margin: auto 0 0;
}
.fees__item {
  padding: 35px 0;
  border-top: 1px solid #bdd9e8;
  display: grid;
  grid-template-columns: 292px 1fr 40px;
  gap: 30px;
}
.fees__item:hover .fees__info p {
  color: #82aec5 !important;
}
.fees__item:hover .fees__prev {
  border-radius: 70px;
}
.fees .btn__trans {
  margin: 30px 0 0;
}
.fees__page {
  padding: 90px 0 152px;
}

.stat {
  padding-bottom: 160px;
}
.stat__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 10px;
}
.stat__item {
  border-radius: 20px;
  background: #fff;
  padding: 11px 25px 19px;
  min-height: 380px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.stat__item:nth-child(2) {
  grid-column: 3/4;
}
.stat__val {
  font: 500 70px "SF Pro Display", sans-serif;
  text-transform: uppercase;
  color: #0082c9;
}
.stat__text {
  font: 400 18px/1.4 "SF Pro Display", sans-serif;
  color: #4686a8;
}

.sup {
  padding-top: 98px;
  padding-bottom: 100px;
  color: #fff;
  background: #0082c9;
}
.sup .container__title {
  color: #e8f2ff;
}
.sup__title {
  font: 400 50px/0.9 "Comic CAT", sans-serif;
  text-transform: uppercase;
  color: #fff;
}
.sup__text {
  margin: 25px 0 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 11px;
}
.sup__text p {
  font: 400 18px/1.4 "SF Pro Display", sans-serif;
  color: #d1e9f6;
}
.sup .btn__red {
  margin: 33px 0 0;
  padding: 18px 30px;
}

.method {
  padding-top: 158px;
}
.method__item {
  border-top: 1px solid #bdd9e8;
  padding: 35px 0;
  display: grid;
  grid-template-columns: 290px 1fr 147px;
  gap: 10px;
}
.method__item:last-child {
  padding: 35px 0 0;
}
.method__name {
  font: 500 25px/1.3 "SF Pro Display", sans-serif;
  color: #0082c9;
}
.method__info {
  display: flex;
  flex-direction: column;
}
.method__info p {
  font: 400 18px/1.4 "SF Pro Display", sans-serif;
  color: #4686a8;
  margin: 0 0 10px;
  max-width: 370px;
}
.method__info .btn__trans {
  margin: auto 0 0;
}
.method__prev {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 147px;
  border-radius: 20px;
  background: #fff;
}

.about {
  padding-bottom: 160px;
}
.about__name {
  font: 400 50px/0.9 "Comic CAT", sans-serif;
  text-transform: uppercase;
  color: #0082c9;
}
.about__wrapper {
  margin: 34px 0 0;
  display: grid;
  grid-template-columns: 284px 1fr;
  gap: 20px;
}
.about__text {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.about__text p {
  font: 400 18px/1.4 "SF Pro Display", sans-serif;
  color: #4686a8;
}
.about__text .btn__trans {
  margin: auto 0 0;
}
.about__prev img {
  height: 342px;
  width: 100%;
  object-fit: cover;
  border-radius: 30px;
}

.parthner {
  background: #0082c9;
  position: relative;
  overflow: hidden;
  padding: 100px 0;
}
.parthner .container__title {
  color: #e8f2ff;
}
.parthner__wrapper {
  grid-template-columns: 1fr 1fr 1fr;
  gap: 10px;
  display: grid;
}
.parthner__item {
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
}
.parthner__item img {
  max-width: 200px;
  width: 100%;
  height: 80px;
  object-fit: contain;
}
.parthner__run {
  max-height: 400px;
}

.news {
  padding-top: 159px;
  padding-bottom: 160px;
}
.news__page {
  padding-top: 90px;
}
.news__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
}
.news__prev {
  position: relative;
  overflow: hidden;
  border-radius: 10px;
  transition: all 0.3s;
}
.news__prev img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}
.news__row {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-wrap: wrap;
  gap: 15px;
  margin: 0 0 auto;
}
.news__row p {
  font: 400 14px/1.4 "SF Pro Display", sans-serif;
  color: #77aecc;
}
.news__name {
  font: 500 25px/1.3 "SF Pro Display", sans-serif;
  color: #0082c9;
  max-width: calc(100% - 50px);
  transition: all 0.3s;
}
.news__link {
  position: absolute;
  bottom: 20px;
  right: 25px;
}
.news .btn__trans {
  margin: 37px 0 0;
}
.news__item {
  min-height: 440px;
  display: flex;
  flex-direction: column;
  gap: 17px;
  padding: 25px 25px 15px;
  border-radius: 20px;
  background: #fff;
  position: relative;
}
.news__item:hover .news__prev {
  border-radius: 70px;
}
.news__item:hover .news__name {
  color: #82aec5;
}
.news__check {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}
.news__check div {
  position: relative;
}
.news__check p {
  font: 500 20px/1.2 "SF Pro Display", sans-serif;
  color: #82aec5;
  transition: all 0.3s;
  padding: 13px 19px;
  border: 1px solid #bdd9e8;
  border-radius: 8px;
}
.news__check input {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  opacity: 0;
}
.news__check input:hover + p, .news__check input:checked + p {
  color: #fff;
  background: #0082c9;
  border: 1px solid #0082c9;
}
.news__block {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
  margin: 30px 0 0;
}

.pag {
  margin: 35px 0 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
  border-top: 1px solid #bdd9e8;
  border-bottom: 1px solid #bdd9e8;
}
.pag__arrow path {
  transition: all 0.3s;
}
.pag__arrow:hover path {
  fill: #77AECC;
}
.pag ul {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
}
.pag ul a,
.pag ul p {
  font: 400 18px/1.4 "SF Pro Display", sans-serif;
  color: #82aec5;
}
.pag ul a {
  transition: all 0.3s;
}
.pag ul a:hover, .pag ul a.active {
  color: #0082c9;
}

.footer {
  background: #0082c9;
  padding-top: 97px;
  padding-bottom: 36px;
}
.footer .container__title {
  color: #e8f2ff;
}
.footer__name {
  font: 400 14px/1.4 "SF Pro Display", sans-serif;
  color: #91cbea;
}
.footer__list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 11px;
  row-gap: 49px;
}
.footer__list ul {
  margin: 14px 0 0;
  display: grid;
  grid-template-columns: 1fr;
  gap: 8px;
}
.footer__list ul a {
  font: 400 18px/1.2 "SF Pro Display", sans-serif;
  color: #fff;
  transition: all 0.3s;
}
.footer__list ul a:hover {
  color: #91cbea;
}
.footer__contact {
  margin: 77px 0 0;
  padding: 21px 0 0;
  border-top: 1px solid #349ed7;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 11px;
}
.footer__contact_wrapper {
  grid-column: 1/3;
}
.footer__contact_link {
  margin: 12px 0 0;
  display: grid;
  grid-template-columns: 1fr;
  gap: 6px;
}
.footer__contact_link a {
  font: 500 25px/1.3 "SF Pro Display", sans-serif;
  color: #fff;
  transition: all 0.3s;
}
.footer__contact_link a:hover {
  color: #91cbea;
}
.footer__contact_row {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  margin: 16px 0 0;
  gap: 10px;
}
.footer__contact_row a {
  font: 400 16px/1.4 "SF Pro Display", sans-serif;
  text-align: center;
  color: #fff;
  padding: 9px 15px;
  width: 100%;
  max-width: 150px;
  border: 1px solid #2996d2;
  border-radius: 55px;
  transition: all 0.3s;
  position: relative;
  overflow: hidden;
}
.footer__contact_row a span {
  display: block;
  transition: all 0.4s;
  position: relative;
}
.footer__contact_row a i {
  display: block;
  position: relative;
  overflow: hidden;
}
.footer__contact_row a i::before {
  min-width: max-content;
  display: block;
  text-align: center;
  content: attr(link);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 200%);
  transition: all 0.4s;
}
.footer__contact_row a:hover {
  border: 1px solid #fff;
}
.footer__contact_row a:hover i::before {
  transform: translate(-50%, -50%);
}
.footer__contact_row a:hover span {
  transform: translateY(-200%);
}
.footer__soc {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  gap: 10px;
  margin: 0 0 auto;
}
.footer__soc_wrapper {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.footer__soc a {
  position: relative;
  width: 56px;
  height: 56px;
  border-radius: 100%;
  border: 1px solid #2996d2;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  transition: all 0.4s;
}
.footer__soc a i {
  position: relative;
  overflow: hidden;
}
.footer__soc a i::before {
  content: "";
  position: relative;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 200%);
  transition: all 0.4s;
  background: var(--bg) center no-repeat;
  background-size: contain;
  width: 26px;
  height: 26px;
  display: block;
  position: absolute;
}
.footer__soc a img {
  width: 26px;
  height: 26px;
  display: block;
  position: relative;
  transition: all 0.4s;
}
.footer__soc a:hover {
  border: 1px solid #fff;
}
.footer__soc a:hover i::before {
  transform: translate(-50%, -50%);
}
.footer__soc a:hover img {
  transform: translateY(-200%);
}
.footer__sub {
  margin: 95px 0 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 11px;
}
.footer__sub a {
  font: 400 14px/1.4 "SF Pro Display", sans-serif;
  color: #91cbea;
  text-decoration: underline;
  text-decoration-skip-ink: none;
  display: block;
  transition: all 0.3s;
}
.footer__sub a:hover {
  color: #fff;
}
.footer__sub p {
  font: 400 14px/1.4 "SF Pro Display", sans-serif;
  color: #91cbea;
}
.footer__sub p a {
  display: inline;
  color: #fff;
}
.footer__sub p a:hover {
  color: #91cbea;
}

